
import header from '@/components/header'
import dateSearch from '@/components/dateSearch'
import Divider from 'vux/src/components/divider/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

import { MessageBox, Indicator, Toast } from 'mint-ui'
import API from '@/api/attendance'
import zcImg from '../../assets/attendance/zc.png'
import ycImg from '../../assets/attendance/yc.png'
export default {
data () {
return {
headtitle: '考勤打卡',
starttime: '', // 开始时间
endtime: '', // 结束时间
userid: '',
token: '',
list: [],
allUseLoad: false,
pageNo: 1, // 当前第几页
perpage: 10, // 每页最多十条
endpage: '',
bottomStatus: '',
bottom_tip: '', // 下拉提示语
zcImg: zcImg, // 正常图片
ycImg: ycImg 
}
},
created () {
this.userid = this.$store.getters.getUser.data.userid
this.token = this.$store.getters.getUser.token
this.loadPageList()
},
components: {
Divider,
Spinner,
'v-date-search': dateSearch,
'v-header': header
},

methods: {
loadBottom: function () {

this.more(this.$refs.loadmore.onBottomLoaded) 
},
loadmorechange (status) {
this.bottomStatus = status
if (status !== 'loading' && status === 'drop') {
this.bottom_tip = '释放加载'
} else if (status === 'loading') {
this.bottom_tip = '正在加载中'
}
},
loadPageList: function () {
this.token = this.$store.getters.getUser.token
let { token, pageNo, perpage, userid, starttime, endtime } = this
Indicator.open({
text: '正在加载...',
spinnerType: 'fading-circle'
})
API.KQGLDKLIST(this, { token, pageNo, perpage, userid, starttime, endtime }).then(rep => {
Indicator.close()
let result = rep.body
if (result.success) {
let datalist = rep.body.result
this.list = datalist
if (datalist.length === 0) {
this.isHaveMore(false)
this.bottom_tip = '没有数据'
} else if (datalist.length < 10) {
this.bottom_tip = '数据已全部加载完'
} else {
this.bottom_tip = '下拉加载更多'
}
} else {
MessageBox.alert(result.message, '操作提示')
}
}, (rep) => {
Indicator.close()
Toast({
message: '服务器异常',
position: 'middle',
duration: 1000
})
})
},
more: function (callback) {

this.pageNo = parseInt(this.pageNo) + 1
let { token, pageNo, perpage, userid, starttime, endtime } = this
API.KQGLDKLIST(this, { token, pageNo, perpage, userid, starttime, endtime }).then(rep => {
let result = rep.body
if (result.success) {
let datalist = rep.body.result
if (datalist.length === 0) {
this.isHaveMore(false)
this.bottom_tip = '数据已全部加载完'
callback()
} else {
this.list = this.list.concat(datalist)
this.bottom_tip = '上拉加载更多'
this.isHaveMore(true)
callback()
}
} else {
MessageBox.alert(result.message, '操作提示')
}
}, (rep) => {
Indicator.close()
Toast({
message: '服务器异常',
position: 'middle',
duration: 1000
})
})
},
isHaveMore: function (isHaveMore) {

this.allUseLoad = true
if (isHaveMore) {
this.allUseLoad = false
}
}
}
}




