import BASE_PATH from '@/api/basePath'
const API = {
  INSERTKQRECORD (context, params) {
    return context.$http.post(BASE_PATH + 'zhfwgl/leave/kqgldk/kqgldkAction!insertKqRecord.do', params)
  },
  KQGLDKLIST (context, params) {
    return context.$http.post(BASE_PATH + 'zhfwgl/leave/kqgldk/kqgldkAction!Kqgldklist.do', params)
  },
  QUERYUSERDKTIME (context, params) {
    return context.$http.post(BASE_PATH + 'zhfwgl/leave/kqgldk/kqgldkAction!queryUserDkTime.do', params)
  }

}

export default API



